import React, {useState} from 'react'
import {Button, Input} from '@ya.praktikum/react-developer-burger-ui-components'
import {Link} from 'react-router-dom'
import {updatePassword} from '../../utils/api'

const ResetPasswordPage = () => {
  const [formValue, setFormValue] = useState({
    password: '',
    token: ''
  })

  const inputHandler = (e) => {
    setFormValue(prevState => ({
      ...prevState,
      [e.target.name]: e.target.value
    }))
  }

  const submitHandler = (e) => {
    e.preventDefault()

    updatePassword(formValue.password, formValue.token)
      .then(console.log)
  }

  return (
    <div className='wrapper'>
      <h1 className='text text_type_main-medium'> </h1>
      <form className='form' onSubmit={submitHandler}>
        <div className='mt-6 mb-6 input'>
          <Input
            onChange={inputHandler}
            value={formValue.password}
            name='password'
            type='password'
            icon='ShowIcon'
            placeholder='  ' />
        </div>
        <div className='mb-6 input'>
          <Input
            onChange={inputHandler}
            value={formValue.token}
            name='token'
            type='text'
            placeholder='   ' />
        </div>
        <div className='send_button'>
          <Button></Button>
        </div>
      </form>
      <p
        className='text mt-20 text_type_main-default text_color_inactive isRegistered'>
         ?
        <Link to='/login'></Link>
      </p>
    </div>
  )
}

export default ResetPasswordPage